.home {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.home-content {
  width: 45vw;
}

.home-content h3 {
	font-size: 1.5rem;
	font-weight: 700;
}

.home-content h3:nth-of-type(2) {
	margin-bottom: 1rem;
}

.home-content h1 {
	font-size: 3rem;
	font-weight: 700;
	line-height: 1.5;
}

.home-img img {
	width: 25vw;
}

.social-media a {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	width: 3rem;
	height: 3rem;
	background: transparent;
	border: .2rem solid var(--main-color);
	border-radius: 50%;
	font-size: 1.5rem;
	color: var(--main-color);
	margin: 3rem 1.5rem 3rem 0;
	transition: 0.5s ease;
}

.social-media a:hover {
	background: var(--main-color);
	color: var(--bg-color);
	box-shadow: 0 0 1rem var(--main-color);
}

@media screen and (max-width: 1200px) {
	.home-content {
		width: 35vw;
	}
}

@media screen and (max-width: 700px) {

	.home-content {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}

	.home-content h3 {
		width: 100%;
		font-size: 1rem;
		text-align: left;
	}

	.home-content h1 {
		width: 100%;
		font-size: 2rem;
		text-align: left;
	}

	.social-media a {
		width: 2rem;
		height: 2rem;
		font-size: 1rem;
	}

}
