header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	display: block;
	margin-bottom: auto;
	padding: 1.5rem;	
	background-color: transparent;
	z-index: 1;
}

.header-nav {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-wrap: wrap;
}

.header-sub-nav {
	display: flex;
	width: 30rem;
	justify-content: space-evenly;
	align-items: center;
}

.header-current-link {
	color: var(--main-color);
	background-color: transparent;
	border: none;
	font-size: 2rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

.header-link {
	color: var(--text-color);
	background-color: transparent;
	border: none;
	font-size: 1.125rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
	transition: .5s ease;
}

@media(hover: hover) {

	.header-link:hover {
		color: var(--main-color);
	}

}

.touch-link {
	color: var(--main-color);
}

@media screen and (max-width: 700px) {

	header {
		padding: 0.75rem;
	}

	.header-sub-nav {
		width: 15rem;
	}

	.header-current-link {
		font-size: 1rem;
		text-transform: uppercase;
	}

	.header-link {	
		font-size: 1rem;
	}

}

@media screen and (max-width: 360px) {

	.header-link {	
		font-size: 0.75rem;
	}

}
