.contact {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-container {
  text-align: center;
}

.contact-container form {
  justify-content: center;
  align-items: center;  
  flex-direction: column;
	padding: 2rem;
  width: 25rem;
  color: var(--text-color);
  font-size: 1rem;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
	border-radius: 16px;
}

.contact-container input, textarea {
  margin-bottom: 1rem;
  padding: 1rem;
  width: 100%;
  font-size: 1rem;
  box-sizing: border-box;
	border-radius: 16px;
}

.contact-container h2 {
	margin-bottom: 3rem;
}

.contact-container textarea {
	resize: none;
}

.contact form .btn {
	margin-top: 2rem;
	cursor: pointer;
}

.page-modal-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 1.5rem;
	color: #000000;
	background-color: #ffffff;
	border: none;
  font-family: 'Arial', sans-serif;
	font-size: 1rem;
	font-weight: bold;
	cursor: pointer;
}

@media screen and (max-width: 700px) {

  .contact-container form {
    width: 20rem;
    font-size: 0.75rem;
  }

}
