footer {
	width: 100%;
	display: flex;	
	margin-top: auto;
	padding: 0.3rem;
	color: var(--text-color);
	background-color: var(--text-color);
	border-top-left-radius: 1rem;
	border-top-right-radius: 1rem;
	font-size: 0.9375rem;
	font-weight: bold;
	justify-content: space-evenly;
	align-items: center;
	flex-wrap: wrap;
	background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
}

.footer-text {
	margin: 1rem;
}

.footer-nav {
	display: flex;
	width: 15rem;
	margin-top: 1rem;
	margin-bottom: 1rem;
	justify-content: space-evenly;
	align-items: center;
}

.footer-icon-top a {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding: .8rem;
	color: var(--bg-color);
	background: var(--main-color);
	border-radius: .8rem;
	transition: .5s ease;
}

.footer-icon-top a:hover {
	box-shadow: 0 0 1rem var(--main-color);
}

.footer-icon-top a i {
	font-size: 2.4rem;
	color: var(--bg-color);
}

@media screen and (max-width: 700px) {

	footer {
		padding: 0.15rem;
		font-size: 0.75rem;
	}

}
