.about {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	gap: 2rem;
	background: var(--bg-color);
}

.about-img {
	width: 35vw;
}

.about-img img {
	width: 100%;
	border-radius: 2rem;
}

.about-content {
	width: 35vw;
}

.about-content h2 {
	text-align: left;
	line-height: 1.2;
}

.about-content p {
	font-size: 1.25rem;
	margin: 2rem 0 3rem;
}

.about-card-container {
	display: flex;
	justify-content: space-between;
	justify-content: center;
	width: 100%;
}

.about-card {
	margin: 0.5rem;
	padding: 1rem;
	width: 80%;
	color: var(--text-color);
	background: rgba(255, 255, 255, 0.1);
	border-radius: 16px;
	box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
	backdrop-filter: blur(1px);
	-webkit-backdrop-filter: blur(1px);
	border: 1px solid rgba(255, 255, 255, 0.3);
}

.about-card h3 {
	margin-bottom: 1rem;
	color: var(--main-color);
	font-size: 1.5rem;
	text-align: center;
}

.about-card-list {
	display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
	font-size: 1rem;
	font-weight: bold;
}

.about-card-list div {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.about-card-list span {
	margin-right: 1rem;
}

@media screen and (max-width: 1800px) {
	.about-card-list {
		grid-template-columns: repeat(3, 1fr);
	}
}

@media screen and (max-width: 1500px) {
	.about-card-list {
		grid-template-columns: repeat(2, 1fr);
	}
}


@media screen and (max-width: 700px) {
	.about-content {
		width: 70vw;
	}

	.about-content p {
		font-size: 1rem;
	}

	.about-card h3 {
		margin-bottom: 0.5rem;
		font-size: 1rem;
	}

	.about-card-list {
		gap: 0.5rem;
		font-size: 0.75rem;
	}

	.about-card-list span {
		margin-right: 0.5rem;
	}
}
