.portfolio {
	display: flex;
  justify-content: center;
  align-items: center;
}

.portfolio-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 100%;
	color: #ffffff;
	text-align: center;
}

.portfolio-container h2 {
	margin-bottom: 3rem;
}

.portfolio-content {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.portfolio-content-btn {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 1rem;
	color: var(--text-color);
	font-size: 2rem;
	cursor: pointer;
	transition: .5s ease;
	background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
	border-radius: 50%;
}

.portfolio-content-outer-box {
	margin-top: 2.5rem;
	width: 20%;
	position: relative;
	border-radius: 50%;
	box-shadow: 0 0 1rem var(--bg-color);
	overflow: hidden;
	display: flex;
}

.portfolio-content-outer-box-sec {
	width: 10%;
}

.portfolio-content-image {
	width: 100%;
	transition: .5s ease;
}

.portfolio-content-outer-box:hover .portfolio-content-image {
	transform: scale(1.1);
}

.portfolio-content-inner-box {
	width: 100%;
}

/* .portfolio-content-name {
	font-size: 1.3125rem;
	font-weight: bold;
} */

.portfolio-content-outer-box:hover .portfolio-content-name {
	display: none;
}

.portfolio-in-progress {
	width: 20rem;
}

.portfolio-layer {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: linear-gradient(rgba(0, 0, 0, .1), var(--main-color));
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	text-align: center;
	padding: 0 4rem;
	transform: translateY(100%);
	transition: .5s ease;
}

.portfolio-layer-sec {
	padding: 0 2rem;
}

.portfolio-content-outer-box:hover .portfolio-layer {
	transform: translateY(0);
}

.portfolio-layer h4 {
	margin-bottom: 1rem;
	font-size: 2rem;
}

.portfolio-layer-sec h4 {
	margin-bottom: 0.5rem;
	font-size: 1rem;
}

.portfolio-layer a {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 0.5rem;
	width: 3rem;
	height: 3rem;
	background-color: transparent;
	border: .2rem solid var(--text-color);
	border-radius: 50%;
	font-size: 2rem;
	color: var(--text-color);
	transition: 0.5s ease;
	cursor: pointer;
}

.portfolio-layer-sec a {
	margin: 0.25rem;
	width: 2rem;
	height: 2rem;
	font-size: 1rem;
}

.portfolio-layer a:hover {
	background-color: var(--text-color);;
	color: var(--bg-color);
	border: .2rem solid var(--bg-color);
	box-shadow: 0 0 1rem var(--bg-color);
}

.portfolio-layer-links {
	display: flex;
	justify-content: center;
	align-items: center;
}

@media(hover: hover) {

	.portfolio-content-btn:hover {
		color: #00ffff;
	}

	.portfolio-content-link:hover {
		color: #00ffff;
	}

}

.touch-link {
	color: #00ffff;
}

@media screen and (max-width: 1500px) {
	.portfolio-layer h4 {
		margin-bottom: 0.75rem;
		font-size: 1.5rem;
	}
	
	.portfolio-layer-sec h4 {
		margin-bottom: 0.3rem;
		font-size: 0.75rem;
	}
	
	.portfolio-layer a {
		margin: 0.3rem;
		width: 2rem;
		height: 2rem;
		font-size: 1.5rem;
	}
	
	.portfolio-layer-sec a {
		margin: 0.2rem;
		width: 1.5rem;
		height: 1.5rem;
		font-size: 0.75rem;
	}
}

@media screen and (max-width: 1200px) {
	.portfolio-layer h4 {
		margin-bottom: 0.75rem;
		font-size: 1.5rem;
	}
	
	.portfolio-layer-sec h4 {
		margin-bottom: 0.3rem;
		font-size: 0.75rem;
	}
	
	.portfolio-layer a {
		margin: 0.3rem;
		width: 2rem;
		height: 2rem;
		font-size: 1.5rem;
	}
	
	.portfolio-layer-sec a {
		margin: 0.2rem;
		width: 1.5rem;
		height: 1.5rem;
		font-size: 0.75rem;
	}
}

@media screen and (max-width: 700px) {

	.portfolio-container h2 {
		margin-bottom: 1.5rem;
	}

	.portfolio-content-outer-box {
		width: 40%;
	}

	.portfolio-content-btn {
		font-size: 1rem;
	}

	/* .portfolio-content-name {
		font-size: 1rem;
	} */

	.portfolio-in-progress {
		width: 10rem;
	}
}
