* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	text-decoration: none;
	border: none;
	outline: none;
	scroll-behavior: smooth;
	font-family: 'Arial', sans-serif;
}

:root {
	--bg-color: #000;
	--text-color: #fff;
	--main-color: #0ef;
}

html {
	font-size: 16px;
	overflow-x: hidden;
}

body {
	background-color: var(--bg-color);
	color: var(--text-color);
	background-size: 100%;
	background-repeat: no-repeat;
  background-attachment: fixed;
}

section {
	margin: 0;
	min-height: 100vh;
}

span {
	color: var(--main-color);
}

.btn {
	display: inline-block;
	padding: 1rem 1.5rem;
	background: var(--main-color);
	border-radius: 4rem;
	box-shadow: 0 0 1rem var(--main-color);
	font-size: 0.75rem;
	color: var(--bg-color);
	letter-spacing: .1rem;
	font-weight: 600;
	transition: .5s ease;
}

.btn:hover {
	box-shadow: none;
}

.heading {
	text-align: center;
	font-size: 3rem;
}

.circular-progress {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
}

.circular-progress svg {
	color: var(--text-color);
}

@media screen and (max-width: 1200px) {
	html {
		font-size: 12px !important;
	}
}

@media screen and (max-width: 700px) {
	section {
		min-height: 70vh;
	}

	.btn {
		padding: 0.75rem 2rem;
		font-size: 0.75rem;
	}

	.heading {
		font-size: 2rem;
	}
	
}
